.popup__container {
  width: 430px;
  background: #FFFFFF;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  padding: 34px 36px 37px;
  box-sizing: border-box;
  position: relative;
}


@media screen and (max-width: 767.98px) {
  .popup__container {
    width: 350px;
    padding: 25px 22px;
  }
}

@media screen and (max-width: 424.98px) {
  .popup__container {
    max-width: 282px;
    margin: 0 19px;
  }
}